// remote actions, defined in OnboardingRemoteActions.component

function callRemoteAction(remoteActionName, ...params) {

    let promise = new Promise( (resolve, reject) => {

        let remoteActionsParams = [window.GanttPalette.remoteActions[remoteActionName]]
            .concat(params)
            .concat(function(data, ev) {
                ev.status ? resolve(data) : reject(ev);
            })
            .concat({ buffer: false, escape: false });

        var remoteActionsParamsNew = remoteActionsParams.map(function(v) { 
            if(v === undefined){
                return null;
            }
            else{
                return v;
            }
        });

        window.Visualforce.remoting.Manager.invokeAction.apply(window.Visualforce.remoting.Manager, remoteActionsParamsNew);

    });

    return promise;

}

export default callRemoteAction;