
import labels from "../../Utils/customLabels";
import React, { PureComponent } from 'react';

class ActivePalette extends PureComponent {

    constructor(props) {
        super(props);
        this.handleCheckboxChange = this.handleCheckboxChange.bind(this);
    }

    render() {

        return (
            <div class="palette-editor-element-wrapper">

                {
                    this.props.isMaxNumberOfActivePalettes

                        ?

                        <div class="slds-form-element slds-has-error">
                            <div class="slds-form-element__control">
                                        <span class="slds-checkbox">
                                            <abbr class="slds-required" title="required">*</abbr>
                                            <input type="checkbox" name="options" id="checkbox-6" onChange={this.handleCheckboxChange} checked={this.props.active} aria-describedby="error_01"/>
                                            <label class="slds-checkbox__label" for="checkbox-6">
                                                <span class="slds-checkbox_faux"></span>
                                                <span class="slds-form-element__label">{labels.Active}</span>
                                            </label>
                                            <div id="error_01" class="slds-form-element__help">{labels.MaxActivePalette_Error}</div>
                                        </span>
                            </div>
                        </div>

                        :

                        <div class="slds-form-element">
                            <div class="slds-form-element__control">
                                        <span class="slds-checkbox">
                                            <input type="checkbox" name="options" id="checkbox-7" onChange={this.handleCheckboxChange} checked={this.props.active}/>
                                            <label class="slds-checkbox__label" for="checkbox-7">
                                                <span class="slds-checkbox_faux"></span>
                                                <span class="slds-form-element__label">{labels.Active}</span>
                                            </label>
                                        </span>
                            </div>
                        </div>

                }
            </div>
        );
    }

    handleCheckboxChange(event){
        this.props.onChange(event.target.checked);
    }
}


export default ActivePalette;