
import React, { Component } from 'react';
import ServiceAppointmentFieldSelector from '../ServiceAppointmentFieldSelector/ServiceAppointmentFieldSelector';
import ColorSchemeEditor from '../ColorSchemeEditor/ColorSchemeEditor';
import labels from '../../Utils/customLabels';
import {FieldTypes} from '../../Misc/ConditionDefenitions';
import callRemoteAction from '../../Utils/callRemoteAction';
import {isEmpty} from '../../Utils/basicOperations';
import PaletteColorLevel from "./PaletteColorLevel";
import ActivePalette from "./ActivePalette";

class GanttPaletteEditor extends Component {

    constructor(props) {
        super(props);

        this.handlePaletteNameChange = this.handlePaletteNameChange.bind(this);
        this.serviceFieldChanged = this.serviceFieldChanged.bind(this);        
        this.saveCurrentPalette = this.saveCurrentPalette.bind(this);
        this.deleteCurrentPalette = this.deleteCurrentPalette.bind(this);
        this.handleColorSchemeChange = this.handleColorSchemeChange.bind(this);
        this.handleCheckboxChange = this.handleCheckboxChange.bind(this);
        this.handlePaletteDescriptionChange = this.handlePaletteDescriptionChange.bind(this);
        this.handlePaletteColorLevelChange = this.handlePaletteColorLevelChange.bind(this);
        this.updateState = this.updateState.bind(this);
        this.onPaletteError = this.onPaletteError.bind(this);

        this.state = {
            palette: this.props.palette,
            serviceAppointmentFieldsMap: undefined,
            fieldDescribe: undefined,
            isSaving: false,
            isPaletteNameBlank: false,
            isMaxNumberOfActivePalettes: false,
            errorInPalette: false,
            descriptionLength: 255 - this.props.palette.description.length,
            nameLength: 80 - this.props.palette.name.length
        };        
        
        this.getServiceAppointmentFieldsMap();
    }
	
    render() {
        return (
            <div class="palette-editor-wrapper">
                { 
                    this.state.serviceAppointmentFieldsMap !== undefined && this.state.palette !== undefined ?
                    
                    <div class="editor-box">
                        <div className={this.state.isPaletteNameBlank ? 'slds-form-element palette-editor-element-wrapper slds-has-error' : 'slds-form-element palette-editor-element-wrapper'}>
                            <label class="slds-form-element__label" for="text-input-id-1">{labels.PaletteName}</label>
                            <div class="slds-form-element__control">
                                <input type="text" id="text-input-id-1" class="slds-input" value={this.state.palette.name} onChange={this.handlePaletteNameChange}/>
                                { this.state.isPaletteNameBlank ? <div id="error-message" class="slds-form-element__help">{labels.PaletteNameEmpty}</div> : null }
                                { this.state.nameLength < 20 ? <div class="slds-form-element__help remaining-text-length" id="error-01">{labels.PaletteDescriptionTooLong.replace('{0}',this.state.nameLength)}</div> : null}
                            </div>
                        </div>

                        <div class="slds-form-element palette-editor-element-wrapper">
                            <label class="slds-form-element__label" for="textarea-id-01">{labels.Description}</label>
                            <div class="slds-form-element__control">
                                <textarea id="textarea-id-01" class="slds-textarea" onChange={this.handlePaletteDescriptionChange} value={this.state.palette.description}/>
                                { this.state.descriptionLength < 50 ?
                                    <div class="slds-form-element__help remaining-text-length" id="error-01">{labels.PaletteDescriptionTooLong.replace('{0}',this.state.descriptionLength)}</div>
                                    : null
                                }
                            </div>
                        </div>

                        <ServiceAppointmentFieldSelector serviceAppointmentFieldsMap={this.state.serviceAppointmentFieldsMap} fieldValue={this.state.palette.serviceProperty} onChange={this.serviceFieldChanged}/>

                        <ColorSchemeEditor colorScheme={this.state.palette.colorScheme} fieldDescribe={this.state.fieldDescribe} OnChange={this.handleColorSchemeChange} OnPaletteError={this.onPaletteError} errorInPalette={this.state.errorInPalette}/>
                        
                        { 
                                this.state.fieldDescribe && this.state.fieldDescribe.Type !== FieldTypes.BOOLEAN && this.state.fieldDescribe.Type !== FieldTypes.PICKLIST  ?
                                <PaletteColorLevel colorLevel={this.state.palette.colorLevel} onChange={this.handlePaletteColorLevelChange}/>   :   null
                        }

                        <ActivePalette isMaxNumberOfActivePalettes={this.state.isMaxNumberOfActivePalettes} active={this.state.palette.active} onChange={this.handleCheckboxChange}/>

                        <div class="slds-form-element palette-editor-element-wrapper flex-box">
                            <div class="align-button-left">
                                <button className={this.state.isSaving ? 'slds-button slds-button slds-button_destructive saving' : 'slds-button slds-button_destructive' } onClick={this.deleteCurrentPalette}>{labels.Delete}</button>
                            </div>
                            <div>
                                {this.state.errorInPalette ?
                                    <button
                                        className={this.state.isSaving ? 'slds-button slds-button_brand saving' : 'slds-button slds-button_brand'}
                                        onClick={this.saveCurrentPalette} disabled>{labels.Save}</button>
                                    :
                                    <button
                                        className={this.state.isSaving ? 'slds-button slds-button_brand saving' : 'slds-button slds-button_brand'}
                                        onClick={this.saveCurrentPalette}>{labels.Save}</button>
                                }
                            </div>
                        </div>
                    </div>

                    :

                    <div>
                        <div role="status" class="slds-spinner slds-spinner_large">
                            <div class="slds-spinner_dot-a"></div>
                            <div class="slds-spinner_dot-b"></div>
                        </div>
                    </div>
                }
            </div>
        );
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.palette.type !== 'div'){
            this.updateState(nextProps);
        }
    }

    onPaletteError(errorInPalette){
        this.setState({errorInPalette: errorInPalette});
    }

    updateState(props){
        if(props.palette && this.state.serviceAppointmentFieldsMap){
            var fieldDescribe = this.state.serviceAppointmentFieldsMap[props.palette.serviceProperty];
            this.setState({palette: props.palette, fieldDescribe: fieldDescribe, isMaxNumberOfActivePalettes: false, errorInPalette: false});
        }
    }

    deleteCurrentPalette(){
        let confirmDelete = window.confirm(labels.areYouSureDeletePalette);

        if(this.state.isSaving === false && confirmDelete){
            this.setState({isSaving: true}, () => {

                let paletteToDelete = this.state.palette;

                callRemoteAction('deleteGanttPalette', paletteToDelete.Id).then(result => {
                    this.setState({isSaving: false, isPaletteNameBlank: false, isMaxNumberOfActivePalettes: false, palette: undefined});
                    this.props.OnPaletteDelete(paletteToDelete.Id);
                }).catch(ex => {
                    this.setState({isSaving: false, isPaletteNameBlank: false, isMaxNumberOfActivePalettes: false, palette: undefined});
                    ex.message && window.alert(ex.message);
                });
            });
        }
    }

    saveCurrentPalette(){
        if(this.state.isSaving === false){
            this.setState({isSaving: true}, () => {
                
                let paletteToSave = this.state.palette;
                
                if(isEmpty(paletteToSave.colorScheme) === false){
                    if(this.state.fieldDescribe.Type === FieldTypes.PICKLIST){
                        var relevantPicklistMap = paletteToSave.colorScheme.picklist[this.state.fieldDescribe.Name];
                        var relevantPicklistMapCopy = JSON.parse(JSON.stringify(relevantPicklistMap));
                        paletteToSave.colorScheme.picklist = {};
                        paletteToSave.colorScheme.picklist[this.state.fieldDescribe.Name] = relevantPicklistMapCopy
                    }
                    else{
                        paletteToSave.colorScheme.picklist = {};
                    }

                    var paletteName = paletteToSave.name.trim();

                    if(!paletteName || paletteName.length === 0){
                        this.setState({ isSaving: false, isPaletteNameBlank: true, isMaxNumberOfActivePalettes: false});
                    }
                    else{

                        callRemoteAction('saveGanttPalette', paletteToSave.Id, paletteToSave.name, paletteToSave.description, paletteToSave.serviceProperty, JSON.stringify(paletteToSave.colorScheme), paletteToSave.colorLevel, paletteToSave.active).then(result => {

                            if(paletteToSave.Id === undefined){
                                paletteToSave.Id = result;
                            }
                            
                            this.setState({isSaving: false, isPaletteNameBlank: false, isMaxNumberOfActivePalettes: false, palette: paletteToSave});
                            this.props.OnPaletteSave(paletteToSave);

                        }).catch(ex => {
                            
                            if(ex.message.includes(labels.MaxActivePalette_Error)){
                                this.setState({ isSaving: false, isMaxNumberOfActivePalettes: true});
                            }
                            else{
                                this.setState({ isSaving: false, isMaxNumberOfActivePalettes: false});
                                ex.message && window.alert(ex.message);
                            }
                        });

                    }
                }
                else{
                    this.setState({ isSaving: false});
                }
            });
        }
    }

    serviceFieldChanged(fieldName){
        var updatedPalette = this.state.palette;
        updatedPalette.serviceProperty = fieldName;
        var fieldDescribe = this.state.serviceAppointmentFieldsMap[updatedPalette.serviceProperty];
        this.setState({palette : updatedPalette, fieldDescribe: fieldDescribe});
    }

    handleColorSchemeChange(colorScheme){
        var updatedPalette = this.state.palette;
        updatedPalette.colorScheme = colorScheme;
        this.setState({palette : updatedPalette, errorInPalette: false});
    }

    handlePaletteNameChange(event){
        if(event.target.value.length <= 80) {
            var updatedPalette = this.state.palette;
            updatedPalette.name = event.target.value;
            this.setState({palette: updatedPalette, nameLength: 80 - event.target.value.length});
        }
    }

    handlePaletteDescriptionChange(event){
        if(event.target.value.length <= 255) {
            var updatedPalette = this.state.palette;
            updatedPalette.description = event.target.value;
            this.setState({palette: updatedPalette, descriptionLength: 255 - event.target.value.length});
        }
    }

    handlePaletteColorLevelChange(value){
        var updatedPalette = this.state.palette;
        updatedPalette.colorLevel = value;
        this.setState({palette : updatedPalette});
    }

    handleCheckboxChange(value){
        var updatedPalette = this.state.palette;
        updatedPalette.active = value;
        this.setState({palette : updatedPalette});        
    }

    getServiceAppointmentFieldsMap(){
        callRemoteAction('getServiceAppointmentFieldsMap').then(result => {

            var newResult = {};

            for(let key in result){
                newResult[key] = JSON.parse(result[key].replace(/&quot;/g,'"'));
            }

            var currentFieldDescribe = newResult[this.state.palette.serviceProperty];

            this.setState({serviceAppointmentFieldsMap: newResult, fieldDescribe: currentFieldDescribe});

        }).catch(ex => {
            // TODO: add exception handling.
        });
    }
}

export default GanttPaletteEditor;