
import React, { Component } from 'react';
import {ServiceDateField,ServiceBooleanField,ServicePicklistField,ServiceLocationField,ServiceNumberField,ServicePercentField} from './ServiceTypeTemplates/ServiceTypeTemplates';
import {FieldTypes} from './../../Misc/ConditionDefenitions';

// PROPS
// colorScheme - color scheme to edit.
// type - the type of the field to edit the colot scheme by.
// OnChange - activate the parent function on color scheme change pass the new color scheme as argument.

class ColorSchemeEditor extends Component {

    constructor(props) {
        super(props);
        this.OnChange = this.OnChange.bind(this);
        this.initilizeColorScheme = this.initilizeColorScheme.bind(this);

        var colorScheme = this.initilizeColorScheme(this.props);

        this.state = {
            colorScheme : colorScheme,
            fieldDescribe: this.props.fieldDescribe
        }
    }
	
    render() {
        
        let configField;
        let desc = this.state.fieldDescribe ? this.state.fieldDescribe.Type : undefined;

        switch (desc) {
            case FieldTypes.BOOLEAN:
                configField = <ServiceBooleanField colorScheme={this.state.colorScheme} OnChange={this.OnChange} OnPaletteError={this.props.OnPaletteError} errorInPalette={this.props.errorInPalette}/>;
                break;
            case FieldTypes.DATE:
                configField = <ServiceDateField colorScheme={this.state.colorScheme} OnChange={this.OnChange} OnPaletteError={this.props.OnPaletteError} errorInPalette={this.props.errorInPalette}/>;
                break;
            case FieldTypes.DATETIME:
                configField = <ServiceDateField colorScheme={this.state.colorScheme} OnChange={this.OnChange} OnPaletteError={this.props.OnPaletteError} errorInPalette={this.props.errorInPalette}/>;
                break;
            case FieldTypes.DOUBLE:
                configField = <ServiceNumberField colorScheme={this.state.colorScheme} OnChange={this.OnChange} step="0.1" OnPaletteError={this.props.OnPaletteError} errorInPalette={this.props.errorInPalette}/>;
                break;
            case FieldTypes.INTEGER:
                configField = <ServiceNumberField colorScheme={this.state.colorScheme} OnChange={this.OnChange} step="1" OnPaletteError={this.props.OnPaletteError} errorInPalette={this.props.errorInPalette}/>;
                break;
            case FieldTypes.PICKLIST:
                configField = <ServicePicklistField colorScheme={this.state.colorScheme} OnChange={this.OnChange} fieldDescribe={this.state.fieldDescribe} OnPaletteError={this.props.OnPaletteError} errorInPalette={this.props.errorInPalette}/>;
                break;
            case FieldTypes.LOCATION:
                configField = <ServiceLocationField colorScheme={this.state.colorScheme} OnChange={this.OnChange} OnPaletteError={this.props.OnPaletteError} errorInPalette={this.props.errorInPalette}/>;
                break;            
            case FieldTypes.ADDRESS:
                configField = <ServiceLocationField colorScheme={this.state.colorScheme} OnChange={this.OnChange} OnPaletteError={this.props.OnPaletteError} errorInPalette={this.props.errorInPalette}/>;
                break;
            case FieldTypes.PERCENT:
                configField = <ServicePercentField colorScheme={this.state.colorScheme} OnChange={this.OnChange} step="1" OnPaletteError={this.props.OnPaletteError} errorInPalette={this.props.errorInPalette}/>;
                break;
            case FieldTypes.CURRENCY:
                configField = <ServiceNumberField colorScheme={this.state.colorScheme} OnChange={this.OnChange} step="0.5" OnPaletteError={this.props.OnPaletteError} errorInPalette={this.props.errorInPalette}/>;
                break;
            default: 
                configField = <span></span>;
       }

        return (
            <div>
                {configField}
            </div>
        );
    }

    componentDidMount(){
        this.props.OnChange(this.state.colorScheme);
    }

    componentWillReceiveProps(nextProps){
        var colorScheme = this.initilizeColorScheme(nextProps);
        this.setState({colorScheme : colorScheme, fieldDescribe: nextProps.fieldDescribe});
    }

    initilizeColorScheme(props){
        var colorScheme = props.colorScheme;

        if(colorScheme === undefined){
            colorScheme = {
                min: {
                    color: "#79c879",
                    value: 0,
                    type: "Minutes"
                },
                max: {
                    color: "#ff4646",
                    value: 1,
                    type: "Minutes"
                },
                empty: "#d4504c",
                picklist: {},
                geolocation: {
                    latitude: 0,
                    longitude: 0
                }
            };
        }
        else{
            colorScheme = Object.assign({},{...props.colorScheme});
        }

        return colorScheme;
    }

    OnChange(colorScheme){
        this.props.OnChange(colorScheme);
    }
}

export default ColorSchemeEditor;
