
import React, { Component } from 'react';
import labels from '../../Utils/customLabels';
import {lsdIcons} from "../../Misc/ConditionDefenitions";

class ServiceAppointmentFieldSelector extends Component {

    constructor(props) {
        super(props);

        this.showHelp = this.showHelp.bind(this);
        this.hideHelp = this.hideHelp.bind(this);
        this.change = this.change.bind(this);
        
        let options = [];

        for(let key in this.props.serviceAppointmentFieldsMap){
            let currentFieldDescribe = this.props.serviceAppointmentFieldsMap[key];
            options.push(<option value={currentFieldDescribe.Name}>{currentFieldDescribe.Label}</option>);
        }

        this.state = {
            serviceFieldsOptions: options,
            showHelp: false
        };
    }
	
    render() {
        return (
            <div class="slds-form-element palette-editor-element-wrapper">
                <label class="slds-form-element__label" for="select-01">{labels.BasedOn}</label>

                {
                    this.state.showHelp ?

                        <div class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left help-text-value" id="help" role="tooltip">
                            <div class="cover-helper" onClick={ this.hideHelp }/>
                            <div class="slds-popover__body slds-text-longform">
                                <p>{labels.PaletteServiceFieldTooltip}</p>
                            </div>
                        </div>

                        :

                        null
                }


                <div class="slds-form-element__icon">
                    <button aria-describedby="help" class="slds-button slds-button_icon" onClick={this.showHelp}>
                        <svg class="slds-icon slds-icon_x-small slds-icon-text-default" aria-hidden="true">
                            <use href={lsdIcons.info} />
                        </svg>
                    </button>
                </div>

                <div class="slds-form-element__control">
                    <div class="slds-select_container">
                        <select class="slds-select" id="select-01" onChange={this.change} value={this.props.fieldValue}>
                            {this.state.serviceFieldsOptions}
                        </select>
                    </div>
                </div>
            </div>
        );
    }

    change(event){
        this.props.onChange(event.target.value);
    }

    showHelp(){
        this.setState({ showHelp: true});
    }

    hideHelp(){
        this.setState({ showHelp: false });
    }
}

export default ServiceAppointmentFieldSelector;
