
import React, { Component } from 'react';
import { SliderPicker } from 'react-color';
import reactCSS from 'reactcss'
import {lsdIcons} from '../../../Misc/ConditionDefenitions';
import labels from '../../../Utils/customLabels';

// 3 Props Available: 
// OnChange method provides a way to act on selection of a color the method provided will return the hex value of the color selected.
// initialColor property for the color box 
// label - text to put on the right side of the color 

let inputIdNumber = 1;

class ColorSelector extends Component {

    constructor(props) {
        super(props);

		this.handleChangeComplete = this.handleChangeComplete.bind(this);
		this.handleChange = this.handleChange.bind(this);
		this.handleHexChange = this.handleHexChange.bind(this);
		this.handleClick = this.handleClick.bind(this);
		this.handleClose = this.handleClose.bind(this);
		this.HexToColor = this.HexToColor.bind(this);
		
		let newColor = this.HexToColor(this.props.color);

		this.state = {
			displayColorPicker: false,
			color: newColor,
			showError: false
		};

		this.inputIdName = "input-color-selector-" + inputIdNumber++;
    }
    
    render() {

	    const styles = reactCSS({
	      'default': {
	        color: {
	          width: '36px',
	          height: '15px',
	          borderRadius: '2px',
	          marginRight: '2px'
	        },
	        pickercolor: {
	          background: `rgba(${ this.state.color.rgb.r }, ${ this.state.color.rgb.g }, ${ this.state.color.rgb.b }, ${ this.state.color.rgb.a })`,
	        },
	        swatch: {
	          padding: '5px',
	          background: '#fff',
	          borderRadius: '2px',
	          boxShadow: '0 0 0 1px #d8dde7',
	          display: 'inline-flex',
	          cursor: 'pointer',
	          float: 'right',
	          minWidth: '93px'
	        },
	        popover: {
				position: 'absolute',
				zIndex: '2',
				width: '60%',
				minWidth: '243px',
				backgroundColor: '#ffffff',
				border: '1px solid #d8dde6',
				padding: '10px',
    			margin: '-32px 0px 0px 161px',
    			boxShadow: '0 2px 3px 0 rgba(0, 0, 0, 0.16)'
	        },
	        before : { 
	        	popover:  {
	        		content: '',
				    display: 'block',
				    width: '0',
				    height: '0', 
				    borderTop: '12px solid transparent',
				    borderBottom: '12px solid transparent',
				    borderRight: '12px solid #f5f5f5',
				    position: 'absolute',
				    top: '16px',
				    left: '-12px',
				}
			},
	        cover: {
	          position: 'fixed',
	          top: '0px',
	          right: '0px',
	          bottom: '0px',
	          left: '0px',
	        },
	        slider: {
	          position: 'relative',
	          padding: '5px'
	        },
	        container: {
    			borderBottom: '1px solid #d8dde7',
    			maxWidth: '299px',
    			minHeight: '31px',
    			marginBottom: '20px'
	    	},
	    	label:{
			    padding: '6px',
			    display: 'inline-flex',
			},
			hexinput :{
			    maxWidth: '50px',
			    maxHeight: '10px',
			}
	      },
	    });

        return (

			<div className={this.state.showError ? 'slds-form-element palette-editor-element-wrapper slds-has-error' : 'slds-form-element palette-editor-element-wrapper'}>

				<label class="slds-form-element__label" for={this.inputIdName}>{this.props.translation}</label>

			    <div class="slds-color-picker">
					<div class="slds-color-picker__summary">
						<div class="slds-form-element slds-color-picker__summary-input color-text-box">
							<div class="slds-form-element__control">
								<input type="text" id={this.inputIdName} class="slds-input input-normal-line-height" value={this.state.color.hex} onChange={this.handleHexChange}/>
								{this.state.showError === true ? <div id="error-message" class="slds-form-element__help">{labels.BadHexValueError}</div> : null }
							</div>
						</div>
						<span class="slds-button slds-button_icon slds-button_icon-more color-swatch-button" onClick={ this.handleClick }>
							<span class="slds-swatch color-swatch-box" style={styles.pickercolor}/>
							<svg class="slds-button__icon slds-button__icon_small slds-m-left_xx-small color-swatch-box" aria-hidden="true">
								<use href={lsdIcons.down}/>
							</svg>
						</span>
					</div>
				</div>

		        { 
		        	this.state.displayColorPicker ? 
		        	
		        	<div style={ styles.popover } class="colorbox">
		        		<div style={ styles.cover } onClick={ this.handleClose }/>
		        		<div style={ styles.slider }>
		          			<SliderPicker color={ this.state.color.hsl } onChange={ this.handleChange } />
		          		</div>
		        	</div> 
		        	
		        	:

		        	null 
		    	}
		    </div>
        );
    }

    componentDidMount(){
        let newColor = this.state.color;

        if(newColor.hex[0] !== '#' || newColor.hex.length !== 7 || newColor.hex.substr(1,newColor.hex.length).match("^[a-zA-Z0-9]+$") === null){
            this.props.OnChange(newColor.hex, this.props.label, false);
        }
        else{
            this.props.OnChange(newColor.hex, this.props.label, true);
        }
	}

    componentWillReceiveProps(nextProps){
    	var newColor = this.HexToColor(nextProps.color);

    	if(newColor.hex[0] !== '#' || newColor.hex.length !== 7 || newColor.hex.substr(1,newColor.hex.length).match("^[a-zA-Z0-9]+$") === null){
            this.setState({color: newColor, showError: true});
        }
        else{
        	this.setState({color: newColor, showError: false});
		}
    }

	handleHexChange(event){
		var newColor = this.HexToColor(event.target.value);

		if(newColor.hex[0] !== '#' || newColor.hex.length !== 7 || newColor.hex.substr(1,newColor.hex.length).match("^[a-zA-Z0-9]+$") === null){
            this.props.OnChange(newColor.hex, this.props.label, false);
		}
		else{
			this.props.OnChange(newColor.hex, this.props.label, true);
		}
	}

	handleChange(color){
		this.setState({ color: color });
	}

	handleChangeComplete(color){
		
	}

	handleClick() {
    	this.setState({ displayColorPicker: !this.state.displayColorPicker })
  	}

  	handleClose() {
    	this.setState({ displayColorPicker: false}, this.props.OnChange(this.state.color.hex, this.props.label, true));
  	}

	HexToColor(hex){

		if(hex === undefined){
			hex = '#1589ee';
		}
		
		var rgb = this.hexToRGB(hex);
		var hsl = this.rgbToHSL(rgb);
			
		var color = {
		    	hex: hex,
		    	rgb: rgb,
		    	hsl: hsl 
		    };

		return color;
    }

  	rgbToHSL(rgb) {

  		var r = rgb.r;
	    var g = rgb.g;
	    var b = rgb.b;
	    
	    r /= 255;
	    g /= 255;
	    b /= 255;
	    
	    var max = Math.max(r, g, b), min = Math.min(r, g, b);
	    var h, s, l = (max + min) / 2;
	    
	    if(max === min){
	      h = s = 0; // achromatic
	    }else{
	      
	      var d = max - min;
	      s = l > 0.5 ? d / (2 - max - min) : d / (max + min);
	      
	      switch(max){
	        case r: h = (g - b) / d + (g < b ? 6 : 0); break;
	        case g: h = (b - r) / d + 2; break;
	        case b: h = (r - g) / d + 4; break;
	      }
	      
	      h /= 6;
	  	}

	  	return { h: h , s: s , l: l}
	};

	hexToRGB(hex) {
		hex = typeof hex === 'string' || hex instanceof String ? parseInt(hex.replace(/^#/, ''), 16) : hex;
		return {r:(hex >> 16) & 0xFF, g:(hex >> 8) & 0xFF, b:hex & 0xFF, a: '1'};
	}
}

export default ColorSelector;