
import React, { Component } from 'react';
import GanttPaletteEditor from './Components/GanttPaletteEditor/GanttPaletteEditor';
import GanttPaletteSelector from './Components/GanttPaletteSelector/GanttPaletteSelector';
import {lsdIcons} from './Misc/ConditionDefenitions';
import labels from './Utils/customLabels';
import {fieldNames} from './Utils/fieldAndObjectNames';
import './index.css';

class App extends Component {

    constructor(props) {
        super(props);

        this.setCurrentPalette = this.setCurrentPalette.bind(this);
        this.createNewPalette = this.createNewPalette.bind(this);
        this.addSavedPalette = this.addSavedPalette.bind(this);
        this.removeDeletedPalette = this.removeDeletedPalette.bind(this);

        this.state = {  
            currentPalette: undefined,
            paletteToSave: undefined,
            paletteToRemove: undefined
        };
    }

    render() {
        return (
          <div class="app-box">
            <div class="section-title">
                <div class="top-page-item-left">
                    <span class="slds-icon_container slds-icon-custom-custom5 icon-margin">
                        <svg class="slds-icon slds-icon-text-default slds-icon_large icon-on-header" aria-hidden="true">
                            <use xlinkHref={lsdIcons.palette}></use>
                        </svg>
                    </span>
                    {labels.GanttPalettes}
                </div>
                <div class="top-page-item-right">
                    <a class="slds-button slds-button_brand create-palette-button" onClick={this.createNewPalette}>{labels.CreateNewPalette}</a>
                </div>
            </div>
            <div class="main-container">
                <div class="left-pane">
                    <GanttPaletteSelector currentPalette={this.state.currentPalette} OnSelect={this.setCurrentPalette} lastSavedPalette={this.state.paletteToSave} lastRemovedPalette={this.state.paletteToRemove}/>
                </div>
                <div class="right-pane">
                    {   
                        this.state.currentPalette !== undefined ?  
                        
                            <GanttPaletteEditor palette={this.state.currentPalette} OnPaletteSave={this.addSavedPalette} OnPaletteDelete={this.removeDeletedPalette} />
                        
                            : 

                            null
                    }
                </div>
            </div>
          </div>
        );
    }

    addSavedPalette(paletteToAdd){
        this.setState({ paletteToSave: paletteToAdd, paletteToRemove: undefined });
    }

    removeDeletedPalette(paletteToRemove){
        this.setState({ paletteToRemove: paletteToRemove ,paletteToSave: undefined, currentPalette: undefined});
    }

    createNewPalette(){ 

        this.setState((prevState) => {

            var newPaletteTemplate = {
                Id: undefined,
                name: "",
                serviceProperty: fieldNames.DueDate,
                colorScheme: {
                    min: {
                        color: "#79c879",
                        value: 0,
                        type: "Minutes"
                    },
                    max: {
                        color: "#ff4646",
                        value: 1,
                        type: "Minutes"
                    },
                    empty: "#d4504c",
                    picklist: {},
                    geolocation: {
                        latitude: 0,
                        longitude: 0
                    }
                },
                active: false,
                description: "",
                colorLevel: 6
            };

            return { currentPalette: newPaletteTemplate, paletteToSave: undefined, paletteToRemove: undefined};
        });
    }

    setCurrentPalette(currentPalette){

        var paletteString = JSON.stringify(
            currentPalette,
            function(key, val) { if (val === undefined && key !== 'Id') { return ""; } return val; }
        );

        var paletteClone = JSON.parse(paletteString);

        this.setState({currentPalette: paletteClone, paletteToSave: undefined, paletteToRemove: undefined});
    }    
}

export default App;