
import React, { PureComponent } from 'react';
import callRemoteAction from '../../Utils/callRemoteAction';
import {isEmpty} from '../../Utils/basicOperations';
import {fieldNames} from '../../Utils/fieldAndObjectNames';
import {lsdIcons} from '../../Misc/ConditionDefenitions';
import labels from '../../Utils/customLabels';


class GanttPaletteSelector extends PureComponent {

    constructor(props) {
        super(props);

        this.selectExistingPalette = this.selectExistingPalette.bind(this);
        this.parseExistingPalettesList = this.parseExistingPalettesList.bind(this);
        this.filterPalettes = this.filterPalettes.bind(this);
        
        this.state = {
            existingPalettes : undefined,
            filterQuery: undefined,
            currentKey: undefined
        };

        this.getExistingGanttPalettes();
    }
	
    render() {

        var palettesList = this.parseExistingPalettesList();

        return (
            <div>
                <nav class="slds-nav-vertical" aria-label="Sub page">

                    <div class="slds-form-element slds-col slds-p-horizontal_medium">
                        <div class="slds-form-element__control slds-input-has-icon slds-input-has-icon_left">
                            <svg class="slds-icon slds-input__icon slds-input__icon_right slds-icon-text-default" aria-hidden="true">
                                <use href={lsdIcons.search}/>
                            </svg>
                            <input type="search" id="input-id-01" placeholder={labels.QuickFind} class="slds-input" onChange={this.filterPalettes}/>
                        </div>
                    </div>

                    <div class="slds-form-element slds-col slds-p-horizontal_medium navigation-bar-section">
                        <h2 id="entity-header" class="slds-text-title_caps">{labels.Palettes}</h2>
                    </div>

                    <div class="slds-form-element slds-col slds-p-horizontal_medium">
                        <ul>
                            {palettesList}
                        </ul>
                    </div>
                </nav>
            </div>
        );
    }

    parseExistingPalettesList(){
        
        var palettesList = [];

        if(isEmpty(this.state.existingPalettes) === false){
            for(let key in this.state.existingPalettes) {
                let key2 = key;
                
                if(isEmpty(this.state.filterQuery) || this.state.existingPalettes[key].name.toLowerCase().indexOf(this.state.filterQuery.toLowerCase()) > -1){
                    palettesList.push(<li title={this.state.existingPalettes[key2].name} className={key2 === this.state.currentKey ? 'slds-nav-vertical__item cancel-margin slds-is-active' : 'slds-nav-vertical__item cancel-margin' }>
                                        <span class="slds-nav-vertical__action palette-picker-text-overflow" onClick={this.selectExistingPalette.bind(this, key2)}>
                                            <span class="li-palette-name-span">{this.state.existingPalettes[key2].name}</span>
                                            { this.state.existingPalettes[key2].active === true ?
                                                
                                                    <span title={labels.PaletteIsActive} class="active-mark-span">
                                                        <svg class="slds-icon slds-icon-text-default slds-icon_x-small active-mark-svg" aria-hidden="true">
                                                            <use class="active-mark-svg" href={lsdIcons.check}/>
                                                        </svg>
                                                    </span>

                                                    :
                                                    
                                                    null
                                            }
                                        </span>
                                      </li>);
                }
            }
        }

        if(isEmpty(palettesList) === true){
            palettesList.push(<li class='slds-nav-vertical__item cancel-margin'><a class="slds-nav-vertical__action palette-picker-text-overflow" aria-describedby="entity-header" aria-current="page">{labels.NoPalettes}</a></li>);
        }

        return palettesList;
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.lastSavedPalette !== undefined && nextProps.lastRemovedPalette === undefined){

            this.setState((prevState) => {
                
                if(nextProps.lastRemovedPalette !== undefined && prevState.existingPalettes[nextProps.lastRemovedPalette] !== undefined){
                    delete prevState.existingPalettes[nextProps.lastRemovedPalette];
                }

                prevState.existingPalettes[nextProps.lastSavedPalette.Id] = nextProps.lastSavedPalette;

                return { currentKey: nextProps.lastSavedPalette.Id, existingPalettes : prevState.existingPalettes}
            });
        }
        else if(nextProps.lastRemovedPalette !== undefined && nextProps.lastSavedPalette === undefined){
            this.setState((prevState) => {

                if(nextProps.lastRemovedPalette !== undefined && prevState.existingPalettes[nextProps.lastRemovedPalette] !== undefined){
                    delete prevState.existingPalettes[nextProps.lastRemovedPalette];
                }

                return { currentKey: undefined, existingPalettes : prevState.existingPalettes}
            });
        }
        else{
            if(nextProps.currentPalette !== undefined){
                this.setState({ currentKey: nextProps.currentPalette.Id});
            }
        }
    }

    selectExistingPalette(key){
        this.props.OnSelect(this.state.existingPalettes[key]);
    }

    filterPalettes(event){
        this.setState({filterQuery: event.target.value});
    }

    getExistingGanttPalettes(){
        callRemoteAction('getExistingGanttPalettes').then(result => {

            var existingPalettes = {};

            for(let key in result){
                
                var colorScheme = result[key][fieldNames.ColorScheme__c];

                if(colorScheme !== undefined){
                    try{
                        colorScheme = JSON.parse(colorScheme);
                    }catch(err){
                        colorScheme = undefined;
                    }
                }

                var currentPalette = {
                    Id: key,
                    name: result[key][fieldNames.Name],
                    serviceProperty: result[key][fieldNames.ServiceProperty__c],
                    colorScheme: colorScheme,
                    active: result[key][fieldNames.Active__c],
                    description: result[key][fieldNames.Description__c],
                    colorLevel: result[key][fieldNames.ColorLevel__c]
                };

                existingPalettes[key] = currentPalette;
            }
            
            this.setState({existingPalettes: existingPalettes});

        }).catch(ex => {
            // TODO: add exception handling.
        });
    }
}

export default GanttPaletteSelector;
