
import React, { Component } from 'react';
import labels from '../../Utils/customLabels';
import { lsdIcons } from '../../Misc/ConditionDefenitions';

class PaletteColorLevel extends Component {

    constructor(props) {
        super(props);

        this.showHelp = this.showHelp.bind(this);
        this.hideHelp = this.hideHelp.bind(this);
        this.handlePaletteColorLevelChange = this.handlePaletteColorLevelChange.bind(this);

        this.state = {
            showHelp: false
        };
    }

    render() {
        return (
            <div class="slds-form-element palette-editor-element-wrapper">

                <label class="slds-form-element__label slds-align-middle" for="form-help">{labels.PaletteColorLevel}</label>

                {
                    this.state.showHelp ?

                        <div class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left help-text-color-level" id="help" role="tooltip">
                            <div class="cover-helper" onClick={ this.hideHelp }/>
                            <div class="slds-popover__body slds-text-longform">
                                <p>{labels.PaletteColorLevelTooltip}</p>
                            </div>
                        </div>

                        :

                        null
                }

                <div class="slds-form-element__icon">
                    <button aria-describedby="help" class="slds-button slds-button_icon" onClick={this.showHelp}>
                        <svg class="slds-icon slds-icon_x-small slds-icon-text-default" aria-hidden="true">
                            <use href={lsdIcons.info} />
                        </svg>
                    </button>
                </div>
                <div class="slds-form-element__control">
                    <div class="slds-slider">
                        <input type="range" id="slider-id-01" class="slds-slider__range" value={this.props.colorLevel} onChange={this.handlePaletteColorLevelChange} min="4" max="15"/>
                        <span class="slds-slider__value" aria-hidden="true">{this.props.colorLevel}</span>
                    </div>
                </div>
            </div>
        );
    }

    handlePaletteColorLevelChange(event){
        this.props.onChange(event.target.value);
    }

    showHelp(){
        this.setState({ showHelp: true});
    }

    hideHelp(){
        this.setState({ showHelp: false });
    }
}

export default PaletteColorLevel;
