class ServicePicklistField extends Component {

    constructor(props) {
        super(props);
        
        this.changeColor = this.changeColor.bind(this);
        this.getPicklistValues = this.getPicklistValues.bind(this);
        this.parsePicklistColors = this.parsePicklistColors.bind(this);

        this.state = this.getPicklistValues(this.props.colorScheme);
    }
  
    render() {

        let picklistColors = this.parsePicklistColors();

        return (
            <div>
                {picklistColors}
                <div>
                   <span>
                        <ColorSelector color={this.state.colorScheme.empty} OnChange={this.changeColor} label={labels.NoValue} translation={labels.NoValue}/>
                   </span> 
                </div>
            </div>
        );
    }

    componentWillReceiveProps(nextProps){
        if(!nextProps.errorInPalette) {
            this.setState({colorScheme: nextProps.colorScheme});
        }
    }

    parsePicklistColors(){
        let picklistColors = [];
        let picklistMap = this.state.colorScheme.picklist[this.props.fieldDescribe.Name];

        for(let key in picklistMap) {

            let translatedValue = this.props.fieldDescribe.PicklistLabels[key] || key;

            picklistColors.push(<div>
                                    <span>
                                        <ColorSelector 
                                            color={this.state.colorScheme.picklist[this.props.fieldDescribe.Name][key]}
                                            OnChange={this.changeColor} 
                                            label={key}
                                            translation={translatedValue} />
                                    </span>
                                </div>);
        }

        return picklistColors;
    }

    changeColor(hex, key, isValid){
        let newColorScheme = this.state.colorScheme;

        if(key !== labels.NoValue){
            newColorScheme.picklist[this.props.fieldDescribe.Name][key] = hex;
        }
        else{
            newColorScheme.empty = hex;
        }

        if(isValid) {

            this.state.validColors[key] = true;

            let colorSchemeValid = true;

            for(let validKey in this.state.validColors){
                if(!this.state.validColors[validKey] && validKey !== key){
                    colorSchemeValid = false;
                    break;
                }
            }

            if(colorSchemeValid){
                this.props.OnChange(newColorScheme);
            }
            else{
                this.setState({colorScheme: newColorScheme, validColors: this.state.validColors}, this.props.OnPaletteError(true));
            }
        }
        else{
            let validColorsMap = this.state.validColors;
            validColorsMap[key] = false;
            this.setState({colorScheme: newColorScheme, validColors: validColorsMap}, this.props.OnPaletteError(true));
        }
    }

    getPicklistValues(colorScheme){
         
        if(isEmpty(colorScheme.picklist[this.props.fieldDescribe.Name]) === true){
            let newPicklistMap = {};

            for(let i = 0 ; i < this.props.fieldDescribe.PicklistValues.length ; i++){
                newPicklistMap[this.props.fieldDescribe.PicklistValues[i]] = "#79c879";
            }

            colorScheme.picklist[this.props.fieldDescribe.Name] = newPicklistMap;
        }

        var picklistMap = colorScheme.picklist[this.props.fieldDescribe.Name];
        var validColorsMap = {};

        validColorsMap[labels.NoValue] = true;

        for(let key in picklistMap){
            validColorsMap[key] = true;
        }

        return {colorScheme: colorScheme, validColors: validColorsMap};
    }
}

class ServiceLocationField extends Component {

    constructor(props) { 
        super(props);

        this.setMaxColor = this.setMaxColor.bind(this);
        this.setMinColor = this.setMinColor.bind(this);
        this.setEmptyColor = this.setEmptyColor.bind(this);
        this.handleMinDistanceChanged = this.handleMinDistanceChanged.bind(this);
        this.handleMaxDistanceChanged = this.handleMaxDistanceChanged.bind(this);
        this.handleLatitudeChanged = this.handleLatitudeChanged.bind(this);
        this.handleLongitudeChanged = this.handleLongitudeChanged.bind(this);

        this.state = {
            colorScheme : this.props.colorScheme,
            MinColorValid: true,
            MaxColorValid: true,
            EmptyColorValid: true,
            showError : false,
            minMaxErrorMsg : ''
        }
    }
  
    render() {

        var minUnitLabel = labels.MinimumValue + " (" + distanceUnit + ")";
        var maxUnitLabel = labels.MaximumValue + " (" + distanceUnit + ")";

        return (
            <div>
                <div>
                    <span>
                       <NumberSelector value={this.state.colorScheme.geolocation.latitude} label={labels.Latitude} OnChange={this.handleLatitudeChanged} step="0.000001" showError={false} errorMessage=""/>
                       <NumberSelector value={this.state.colorScheme.geolocation.longitude} label={labels.Longitude} OnChange={this.handleLongitudeChanged} step="0.000001" showError={false} errorMessage=""/>
                   </span>
                </div>
                <div>
                   <span>
                        <NumberSelector value={this.state.colorScheme.min.value} label={minUnitLabel} OnChange={this.handleMinDistanceChanged} step="0.5" help={labels.MinimumValueTooltip} showError={this.state.showError} errorMessage={this.state.minMaxErrorMsg}/>
                        <ColorSelector color={this.state.colorScheme.min.color} OnChange={this.setMinColor} label={labels.MinimumColor}/>
                   </span>
                </div>
                <div>
                   <span>
                        <NumberSelector value={this.state.colorScheme.max.value} label={maxUnitLabel} OnChange={this.handleMaxDistanceChanged} step="0.5" help={labels.MaximumValueTooltip} showError={this.state.showError} errorMessage={this.state.minMaxErrorMsg}/>
                        <ColorSelector color={this.state.colorScheme.max.color} OnChange={this.setMaxColor} label={labels.MaximumColor}/>
                   </span> 
                </div>
                <div>
                   <span>
                        <ColorSelector color={this.state.colorScheme.empty} OnChange={this.setEmptyColor} label={labels.NoValue}/>
                   </span> 
                </div>
            </div>
        );
    }

    componentDidMount(){
        if(parseFloat(this.state.colorScheme.min.value) >= parseFloat(this.state.colorScheme.max.value)){
            this.setState({showError: true, minMaxErrorMsg: labels.MinMaxPaletteError}, this.props.OnPaletteError(true));
        }
    }

    componentWillReceiveProps(nextProps){
        if(!nextProps.errorInPalette) {
            this.setState({colorScheme: nextProps.colorScheme, showError: false, minMaxErrorMsg: ''});
        }
    }

    handleLatitudeChanged(value){
        var newColorScheme = this.state.colorScheme;
        newColorScheme.geolocation.latitude = value;

        if(!this.state.showError && this.state.MinColorValid && this.state.MaxColorValid && this.state.EmptyColorValid) {
            this.props.OnChange(newColorScheme);
        }
        else{
            this.setState({colorScheme: newColorScheme}, this.props.OnPaletteError(true));
        }
    }

    handleLongitudeChanged(value){
        var newColorScheme = this.state.colorScheme;
        newColorScheme.geolocation.longitude = value;

        if(!this.state.showError && this.state.MinColorValid && this.state.MaxColorValid && this.state.EmptyColorValid) {
            this.props.OnChange(newColorScheme);
        }
        else{
            this.setState({colorScheme: newColorScheme}, this.props.OnPaletteError(true));
        }
    }

    handleMinDistanceChanged(value){

        var newColorScheme = this.state.colorScheme;
        newColorScheme.min.value = value;

        if(parseFloat(this.state.colorScheme.max.value) > parseFloat(newColorScheme.min.value)){
            if(this.state.MinColorValid && this.state.MaxColorValid && this.state.EmptyColorValid){
                this.props.OnChange(newColorScheme);
            }
            else{
                this.setState({showError: false, minMaxErrorMsg: '', colorScheme: newColorScheme}, this.props.OnPaletteError(true));
            }
        }
        else{
            this.setState({showError: true, minMaxErrorMsg: labels.MinMaxPaletteError, colorScheme: newColorScheme}, this.props.OnPaletteError(true));
        }
    }

    handleMaxDistanceChanged(value){

        var newColorScheme = this.state.colorScheme;
        newColorScheme.max.value = value;

        if(parseFloat(this.state.colorScheme.min.value) < parseFloat(newColorScheme.max.value)){
            if(this.state.MinColorValid && this.state.MaxColorValid && this.state.EmptyColorValid){
                this.props.OnChange(newColorScheme);
            }
            else{
                this.setState({showError: false, minMaxErrorMsg: '', colorScheme: newColorScheme}, this.props.OnPaletteError(true));
            }
        }
        else{
            this.setState({showError: true, minMaxErrorMsg: labels.MinMaxPaletteError, colorScheme: newColorScheme}, this.props.OnPaletteError(true));
        }
    }

    setMinColor(hex, key, isValid){
        var newColorScheme = this.state.colorScheme;
        newColorScheme.min.color = hex;

        if(!this.state.showError && this.state.MaxColorValid && this.state.EmptyColorValid) {
            this.props.OnChange(newColorScheme);
        }
        else{
            this.setState({MinColorValid: isValid, colorScheme: newColorScheme}, this.props.OnPaletteError(true));
        }
    }

    setMaxColor(hex, key, isValid){
        var newColorScheme = this.state.colorScheme;
        newColorScheme.max.color = hex;

        if(!this.state.showError && this.state.MinColorValid && this.state.EmptyColorValid) {
            this.props.OnChange(newColorScheme);
        }
        else{
            this.setState({MaxColorValid: isValid, colorScheme: newColorScheme}, this.props.OnPaletteError(true));
        }
    }

    setEmptyColor(hex, key, isValid){
        var newColorScheme = this.state.colorScheme;
        newColorScheme.empty = hex;

        if(!this.state.showError && this.state.MinColorValid && this.state.MaxColorValid) {
            this.props.OnChange(newColorScheme);
        }
        else{
            this.setState({EmptyColorValid: isValid, colorScheme: newColorScheme}, this.props.OnPaletteError(true));
        }
    }
}


export {ServiceDateField,ServiceBooleanField,ServicePicklistField,ServiceLocationField,ServiceNumberField,ServicePercentField};
