
import React, { Component } from 'react';
import {lsdIcons} from '../../../Misc/ConditionDefenitions';

class NumberSelector extends Component {

    constructor(props) {
        super(props);
        this.changedValue = this.changedValue.bind(this);
        this.showHelp = this.showHelp.bind(this);
        this.hideHelp = this.hideHelp.bind(this);

        this.state = {
        	showError: this.props.showError,
			errorMessage: this.props.errorMessage,
        	showHelp : false
        }
    }
    
    render() {
        return (
 
			<div className={this.state.showError ? 'slds-form-element palette-editor-element-wrapper slds-has-error' : 'slds-form-element palette-editor-element-wrapper'}>
				<label class="slds-form-element__label slds-align-middle" for="form-help">{this.props.label}</label>

				{ 
					this.state.showHelp ? 

					<div class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left help-text-value" id="help" role="tooltip">
		        		<div class="cover-helper" onClick={ this.hideHelp }/>
						<div class="slds-popover__body slds-text-longform">
							<p>{this.props.help}</p>
						</div>
					</div>

					:

					null
				}

                { this.props.help ?
                    <div class="slds-form-element__icon">
                        <button aria-describedby="help" class="slds-button slds-button_icon" onClick={this.showHelp}>
                            <svg class="slds-icon slds-icon_x-small slds-icon-text-default" aria-hidden="true">
                                <use href={lsdIcons.info}/>
                            </svg>
                            <span class="slds-assistive-text">Help</span>
                        </button>
                    </div>
                    : null
                }

				<div class="slds-form-element__control">
					<input value={this.props.value} class="slds-input input-normal-line-height" id="form-help" type="number" onChange={this.changedValue} min={this.props.min} placeholder={this.props.placeholder} step={this.props.step}/>
					{this.state.showError ? <div id="error-message" class="slds-form-element__help">{this.state.errorMessage}</div> : null }
				</div>
			</div>
        );
    }

    componentWillReceiveProps(nextProps){
        this.setState({value: nextProps.value, showError: nextProps.showError, errorMessage: nextProps.errorMessage});
    }

	showHelp(){
		this.setState({ showHelp: true});
	}

	hideHelp(){
		this.setState({ showHelp: false });
	}

    changedValue(event){
		this.props.OnChange(event.target.value);
    }
}

export default NumberSelector;